.table {
  width: 100%; }
  .table .table__th {
    padding: 0 .25rem;
    height: 2.5rem;
    vertical-align: top;
    font-size: 0.9rem;
    text-align: left;
    font-weight: 700; }
    .table .table__th:nth-of-type(1) {
      width: 5rem; }
    .table .table__th:not(.table__th--not-sortable) {
      cursor: pointer;
      transition: color 0.2s; }
      .table .table__th:not(.table__th--not-sortable):hover {
        color: brown; }
        .table .table__th:not(.table__th--not-sortable):hover::after {
          opacity: 1; }
      .table .table__th:not(.table__th--not-sortable)::after {
        content: 'arrow_drop_up';
        font-family: 'Material Icons';
        display: inline-block;
        font-size: 1rem;
        position: relative;
        top: .2rem;
        opacity: 0;
        transition-property: opacity, transform;
        transition-duration: 0.2s; }
    .table .table__th.table__th--sort-asc {
      color: #333333; }
      .table .table__th.table__th--sort-asc::after {
        opacity: 1; }
      .table .table__th.table__th--sort-asc:hover {
        color: #333333; }
        .table .table__th.table__th--sort-asc:hover::after {
          transform: rotate(180deg); }
    .table .table__th.table__th--sort-dec {
      color: #333333; }
      .table .table__th.table__th--sort-dec::after {
        transform: rotate(180deg);
        opacity: 1; }
      .table .table__th.table__th--sort-dec:hover {
        color: #333333; }
        .table .table__th.table__th--sort-dec:hover::after {
          transform: rotate(0); }
  .table .table__tr {
    border-bottom: 1px solid grey; }
    .table .table__tr:nth-of-type(even) {
      background: #f4f4f4; }
  .table .table__td {
