    font-size: 0.9rem;
    position: relative; }
  .table .table__action-icon {
    cursor: pointer;
    font-size: 1.25rem;
    padding: .5rem;
    color: #c0c0c0;
    transition: color 0.2s;
    margin: -.5rem -.25rem; }
    .table .table__action-icon:hover, .table .table__action-icon:focus {
      color: #333333; }
  .table .table__onboarding {
    background: red;
    display: block;
    width: 6rem;
    text-align: center;
    padding: .5rem 0;
    border-radius: 1rem;
    color: white; }
    .table .table__onboarding.table__onboarding--pending {
      background: linear-gradient(45deg, green 0, #00cd00 75%, green 100%); }
    .table .table__onboarding.table__onboarding--completed {
      background: linear-gradient(45deg, #333333 0, #5e5e5e 50%, #333333 100%); }
  .table .table__dots {
    cursor: pointer;
    display: inline-block;
    padding: .5rem; }
    .table .table__dots::before {
      content: '';
      display: block;
      width: .3rem;
      height: .3rem;
      background: grey;
      border-radius: 50%;
      box-shadow: -.5rem 0 0 0 grey, .5rem 0 0 0 grey;
      transition-property: box-shadow, background;
      transition-duration: 0.2s; }
    .table .table__dots:hover::before {
      background: #333333;
      box-shadow: -0.5rem 0 0 0 #333333, 0.5rem 0 0 0 #333333; }
  .table .table__toggler {
    position: absolute;
    z-index: -10; }
    .table .table__toggler:checked + .table__dots::before {
      background: #333333;
      box-shadow: 0 -0.5rem 0 0 #333333, -0.5rem 0.25rem 0 0 #333333, 0.5rem 0.25rem 0 0 #333333, -0.25rem 0.5rem 0 0 #333333, 0.25rem 0.5rem 0 0 #333333, 0rem 0.75rem 0 0 #333333; }
    .table .table__toggler:checked ~ .table__more-actions .table__action {
      height: 3rem;
      opacity: 1; }
  .table .table__more-actions {
    position: absolute;
    z-index: 1;
    right: 0;
    top: 100%;
    background: white;
    box-shadow: 0 0.25rem 1rem rgba(0, 0, 0, 0.15);
    overflow: hidden; }
  .table .table__action {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 0m;
    padding-left: 3rem;
    padding-right: 1.5rem;
    font-size: 1rem;
    background: white;
    position: relative;
    cursor: pointer;
    white-space: nowrap;
    height: 0;
    opacity: 0;
    transition: background 0.2s, box-shadow 0.4s, height 0.4s, opacity 0.2s; }
    .table .table__action::before {
      font-family: 'Material Icons';
      position: absolute;
      left: 1rem;
      top: 50%;
      transform: translate(-50%);
      font-size: 1.25rem; }
    .table .table__action.table__action--edit::before {
      content: 'edit'; }
    .table .table__action.table__action--remove::before {
